import React from 'react';
import { MenuContainer, MenuIcon, MenuDescription, MenuItems, MenuItem } from './styles';
import useSoundButton from "../../hooks/useSoundButton";

// Hook para manejar sonidos
const useSound = (url) => {
  const audioRef = React.useRef(new Audio(url));

  const play = () => {
    if (audioRef.current) {
      audioRef.current.play();
    }
  };

  return { play };
};

// Componente del menú
const Menu = ({ items, menuIconImage, onMenuClick }) => {
  const { playHover, playClick, stopHover, stopClick } = useSoundButton();

  const handleMouseEnter = () => {
    stopHover();
    stopClick();
    playHover();
  };

  const handleMenuItemClick = (item) => {
    stopHover();
    stopClick();
    playClick();
    onMenuClick(item.id); // Llama a la función pasada como prop
  };

  return (
    <MenuContainer id='menu'>
      <MenuIcon>
        <img src={menuIconImage} alt="Menu Icon" />
        <MenuDescription>
          <p className='en'>Rosh Pinah Zinc</p><p className='es'>Zinc de Rosh Pinah</p>
        </MenuDescription>
      </MenuIcon>
      <MenuItems open={true}> {/* Forzar el menú a estar siempre visible */}
        {items.map((item, index) => (
          <MenuItem
            onMouseEnter={handleMouseEnter}
            onClick={() => handleMenuItemClick(item)} // Llama a la función al hacer clic
            id={item.id}
            key={index}
          >
            <img src={item.icon} alt={`Icon ${index}`} />
            <span className='en'>{item.ENdescription}</span><span className='es'>{item.ESdescription}</span>
          </MenuItem>
        ))}
      </MenuItems>
    </MenuContainer>
  );
};

export default Menu;
